import React from "react";
import { Card } from "antd";
import { Button } from "antd";
  
import "antd/dist/antd.css";
  
const GameControls = props => {
  return (
    <Card
      title="Controls"
      style={{ width: "316.5px", height: "280px", alignItems: "center",backgroundColor:"#FFC81D" }}
    >
      <p style={{ alignItems: "center" }}>
        <Button
        style={{backgroundColor:"#48BFB7"}}
          type="dashed"
          size="large"
          shape="round"
          block
          onClick={() => props.wUpdate("r")}
          disabled={!props.isActive}
        >
          Rock
        </Button>
      </p>
  
  
      <p style={{ alignItems: "center" }}>
        {" "}
        <Button
        style={{backgroundColor:"#E45B72"}}
          type="dashed"
          size="large"
          shape="round"
          block
          onClick={() => props.wUpdate("p")}
          disabled={!props.isActive}
        >
          Paper
        </Button>
      </p>
  
  
      <p style={{ alignItems: "center" }}>
        <Button
        style={{backgroundColor:"#BA48BF"}}
          type="solid"
          size="large"
          shape="round"
          block
          onClick={() => props.wUpdate("s")}
          disabled={!props.isActive}
        >
          Scissors
        </Button>
      </p>
  
    </Card>
  );
};
  
export default GameControls;